import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import {
    ButtonBack, ButtonNext,
    CarouselProvider, ImageWithZoom, Slide, Slider, Dot
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import { SERVERURL } from '../../App';

const Gown = () => {
    // const navigate = useNavigate();
    const params = useParams();

    const [Kode] = useState(params.kode);
    const [Data, setData] = useState({
        _id: "",
        kode: "",
        warna: "",
        harga: "",
        favorit: "",
        kategori: "",
        drive: []
    });
    const [driveId, setDriveId] = useState([]);
    const [Recommend, setRecommend] = useState([]);

    useEffect(() => {
        const getData = () => {
            axios.get(SERVERURL + 'api/v1/gown/kode/' + Kode)
                .then(response => {
                    setData(response.data.hasil)
                    setRecommend(response.data.recommend)
                    setDriveId(response.data.hasil.drive)
                })
                .catch((error) => { console.log(error); })
        }
        getData()
    }, [])


    return (
        <div className='Gown_Main'>
            <div className={`${Data.kategori} position-relative shadow-sm`} style={{
                backgroundRepeat: 'no-repeat',
                backgroundSize: 'cover'
            }}>
                <h2 className='display-2 text-white d-flex justify-content-center overflow-hidden position-relative align-items-center mb-0 text-center py-5' style={{
                    textTransform: 'uppercase',
                    zIndex: '10'
                }}>
                    {Data.kategori.replace("_", " ") + "s"}
                </h2>

                <div className='bg-dark position-absolute top-0 start-0 end-0 bottom-0 bg-opacity-50 backdrop_blur'
                    style={{ zIndex: '0' }}></div>
            </div>

            <section className='mt-4 row mx-5'>
                <CarouselProvider
                    visibleSlides={1}
                    totalSlides={driveId.length}
                    step={1}
                    naturalSlideWidth={1}
                    naturalSlideHeight={1}
                    hasMasterSpinner
                    lockOnWindowScroll
                    className='bg-dark bg-opacity-25 backdrop_blur rounded overflow-hidden col p-0'
                    style={{
                        maxWidth: '40rem',
                    }}
                >
                    <Slider className='m-0' >
                        {driveId.map((el, index) => {
                            return (
                                <Slide index={index} key={index}>
                                    <ImageWithZoom src={`https://drive.google.com/uc?export=view&id=${el}`}
                                        className='imageWZoom' />
                                </Slide>
                            )
                        })}
                    </Slider>

                    <div className='container d-flex justify-content-center'>
                        <ButtonBack className='position-absolute top-50 start-0 translate-middle-y bg-transparent d-grid mx-3'>
                            <span className="material-symbols-outlined overflow-hidden">arrow_back</span>
                        </ButtonBack>
                        <ButtonNext className='position-absolute top-50 end-0 translate-middle-y bg-transparent d-grid mx-3'>
                            <span className="material-symbols-outlined overflow-hidden">arrow_forward</span>
                        </ButtonNext>

                        <div className='d-flex position-absolute bottom-0'>
                            {driveId.map((el, index) => {
                                return (<Dot key={index} slide={index} className='btn btn-light my-3 mx-1' />)
                            })}
                        </div>
                    </div>
                </CarouselProvider>

                <div className='col'>
                    <h1 className='display-2 m-0'>{Data.kode}</h1>
                    <h3 className='display-3 m-0'>
                        <span>Rp. </span>
                        {isNaN(Data.harga) ? "Error" : Data.harga.toLocaleString('id-ID', { minimumFractionDigits: 0 })}
                    </h3>
                    <hr className='bg-dark rounded' style={{ height: '5px' }} />
                    <h4 className='ms-3'>Warna: {Data.warna}</h4>
                </div>
            </section>



            <div className='BEEG'>

                <div className='row roc-cols-4'>
                    <h4>You Might Like...</h4>
                    <div className='row row-cols-4' >

                        {/* <CarouselComp gaun={Recommend}/> */}
                        {/* {Recommend.map((props, index) => {
                            return <ListItems gaun={props} key={index} params={params.id} setKode={setKode}/>;
                        })} */}
                    </div>
                </div>
            </div>

        </div>


    )
}

export default Gown;